import { Component, OnInit, OnDestroy, inject } from '@angular/core';
import { MenuItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { OrdenTrabajo } from 'src/app/interfaces/tarea.interface';
import { LayoutService } from 'src/app/layout/service/app.layout.service';
import { UsuarioService } from 'src/app/services/auth/usuario.service';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { TareaService } from 'src/app/services/tarea.service';

@Component({
  templateUrl: './dashboard.component.html',
})
export class DashboardComponent implements OnInit, OnDestroy {

  private readonly usuarioService = inject(UsuarioService);
  private readonly tareaService = inject(TareaService);
  private readonly messageService = inject(CustomMessageService);

  subscription!: Subscription;

  otrasSubscripciones: Subscription[] = [];

  usuarioID: number;

  ordenesTrabajo: OrdenTrabajo[] = null;

  loading = true;
  
  ngOnInit() {
    this.loading = true;

    this.usuarioID = JSON.parse(localStorage.getItem('usuario')).id;

    this.otrasSubscripciones.push(
      this.tareaService.getOrdenesTrabajosDe(this.usuarioID).subscribe(
        (response) => {
          console.log(response);
          this.loading = false;
          this.ordenesTrabajo = response;
        },
        (error) => {
          console.log(error);
          this.loading = false;
          error.error?.detail
            ? this.messageService.error(error.error.detail)
            : error.error.message == 'JWT Token not found'
            ? this.messageService.alert('Sesion Expirada')
            : this.messageService.error(
                'Ocurrio un error desconocido en local'
              );
        }
      )
    )
  }


  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
    this.otrasSubscripciones.forEach((subscripcion) => {
      subscripcion.unsubscribe();
    });
  }
}
